@media screen and (min-width: 768px) and (orientation:landscape) {
    .heroWrapper {
        max-width: 1280px;
        width: 100%;
        height: auto;
        display: flex;
        margin: 0 auto;
        margin-top: 2.5rem;
        margin-bottom: 2.5rem;
    
        .heroTextContainer {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            width: 42%;
            height: auto;
    
            .spanOne {
                width: 95%;
            }
    
            .spanTwo, .spanThree {
                width: 60%;
    
                button {
                    display: flex;
                    width: 100%;
                    height: 64px;
                    background-color: transparent;
                    opacity: 0.8;
                    backdrop-filter: blur(4px);
                    align-items: center;
                    justify-content: center;
                    border-image: linear-gradient(to right, #00C2FF, #FF27C3) 1;
                    z-index: -1;
                }
            }
               
        }
    
        .heroImgContainer {
            width: 58%;
            height: 100%;
    
            img {
                width: 100%;
                height: auto;
            }
        }
    }
}



@media screen and (max-width: 768px) {
    .heroWrapper {
      flex-direction:column-reverse;
      width: 100vw;

      .heroImgContainer {
        width: 100%;

        h1 {
            line-height: 50px;
        }

        .spanOne {
            width: 100%;

            h3 {
                line-height: 40px;
            }
        }
      }

      .heroTextContainer {
        width: 80%;
        padding: 1rem;
        height: 50vh;

        .spanOne {
            width: 100%;

            h3 {
                line-height: normal;
            }
        }

        .spanTwo {
            width: 100%;
        }
      }

    }
  } 